import { observer } from 'mobx-react-lite';
import React from 'react';
import { List } from 'semantic-ui-react';
import { useStore } from '../../../app/stores/store';
import PersonListItem from './PersonListItem';

// Her skal vi somehow bruge en state variabel sat af brugeren til at afgre, om der skal sorteres
// p navn eller p created. Det skal svare til, hvad der blev brugt i List.Handler.Handle-metoden,
// hvilket igen skal svare til samme brugervalg
//
// Det spiller stadig ikke. Du har fet ryddet lidt op i hvordan klienten sorterer, men
// der er bvl med hvordan API'en henter ting op. Hvorfor kommer Anders Buskjr Nielsen
// fr Anders (uden efternavn).. Det er vel fordi det det OrderBy Then By ikke virker efter hensigten..

export default observer(function PersonList() {
    const {personStore} = useStore();
    const {sortedPeople} = personStore;

    return (
        <List divided>
            {sortedPeople.map(person => (
                <PersonListItem key={person.id} person={person} />
            ))}
        </List>
    )
})